





























import Vue from 'vue';
import MyButton from '@/components/MyButton.vue';

export default Vue.extend({
  components: {
    MyButton,
  },
  name: 'Modal',
  props: {
    title: String,
  },
  data() {
    return {
      visible: false,
      pressedValue: '',
    };
  },
  methods: {
    tryClose() {
      this.visible = false;
      this.$emit('okclose');
    },
    open() {
      this.visible = true;
      this.$emit('open');
    },
    closeModal() {
      this.visible = false;
      this.$emit('close');
    },
  },
});
