








import Vue from 'vue';

export default Vue.extend({
  name: 'MyButton',
  props: {
    size: String,
    color: String,
  },
  data() {
    return {
      pressed: '',
      pressedValue: '',
    };
  },
  methods: {
    sendClick() {
      this.$emit('click');
    },
  },
});
